export const datalist_get = 'datalist_get'
export const datalist_collection = 'datalist_collection'
export const datalist_show = 'datalist_show'
export const datalist_login= 'datalist_login'
export const datalist_delete = 'datalist_delete'
/**
 * 
 * @param {获取商品信息} filter 
 */
export function get(filter) {
  return { type: datalist_get, filter }
}

/**
 * 
 * @param {收藏} text 
 */
export function collection(id) {
  return { type: datalist_collection, id}
}
/**
 * 
 * @param {显示收藏} text 
 */
export function show(id) {
  return { type: datalist_show, id}
}

/**
 * 
 * @param {删除} text 
 */
export function delet(id) {
  return { type: datalist_delete, id}
}

/**
 * 
 * @param {登录} index 
 */
export function login(userinfo) {
  return { type: datalist_login, userinfo}
}