import React from 'react'
import {connect} from 'react-redux'
function Cart(props) {
    console.log(props)
    var  len = props.collection.length;
    //if(props.collection[0]){
        //console.log(props.collection[0].title);
    //}
    console.log(len);

    return (
        <div>
            <div>
                {props.collection[0]?'': '现在啥也没存'}              
            </div>
                {
                    props.collection.map((item,idx)=>(
                        <li key={idx} >
                            {props.collection[idx]?props.collection[idx].title: '现在啥也没存'}              
                            <button onClick={()=> props.dispatch({
                                type:'datalist_delete',
                                collection_delete:{
                                    id:idx
                                }
                            })}>删除</button>
                        </li>
                    ))
                }
     </div>
    )
}

export default connect(
    state=>({  
        collection:state.to_collect
    })
)(Cart);