import React from 'react';
import { Button, TabBar } from 'antd-mobile';
import { withRouter } from 'react-router-dom';
import {connect} from 'react-redux'
class Mytab extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            selectedTab: 'blueTab',
        };
    }
    render() {
        const { pathname } = this.props.location;
        if(pathname.split('/').length > 2){
            return null;
        }
        return (
            <div style={ {
                position: 'fixed',
                width: '100%',
                bottom: 0,
                zIndex:1
            } }>
                <TabBar
                    unselectedTintColor="#949494"
                    tintColor="red"
                    barTintColor="white"
                >
                    <TabBar.Item
                        title="首页"
                        key="home"
                        icon={
                            <i className='iconfont icon-Homehomepagemenu'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-Homehomepagemenu'></i>
                        }
                        selected={ pathname === '/' }
                        onPress={ () => {
                            this.props.history.push('/');
                        } }
                        data-seed="logId"
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-icon4'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-icon4'></i>
                        }
                        title="收藏"
                        key="cart"
                        selected={ pathname === '/cart' }
                        onPress={ () => {
                            this.props.history.push('/cart')
                        } }
                        data-seed="logId1"
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-dingdan'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-dingdan'></i>
                        }
                        title="订单列表"
                        key="Friend"
                        selected={ this.state.selectedTab === 'order' }
                        onPress={ () => {
                            this.props.history.push('/order');
                        } }
                    >

                    </TabBar.Item>
                    <TabBar.Item
                        icon={
                            <i className='iconfont icon-wode'></i>
                        }
                        selectedIcon={
                            <i className='iconfont icon-wode'></i>
                        }
                        title="我的淘宝"
                        key="my"
                        selected={ pathname === '/own' }
                        onPress={ () => {
                            this.props.history.push('/own')
                        } }
                    >
                    </TabBar.Item>
                </TabBar>
            </div>
        );
    }
}
export default withRouter(Mytab);