import './details.css'
//import {TabBar} from "antd-mobile";
import React,{useEffect, useState} from 'react'
import {connect} from 'react-redux'

function Details(props){
    function getlistinfo(){
        fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist')
            .then(res=>res.json())
            .then(res=>{
                //console.log(res.data)
                props.dispatch({
                    type:'datalist_get',
                    datalist:{
                        date:res.data
                    }
                })
            })
    }
    getlistinfo();
    var b = props.match.params.id-100;
    console.log(b);
    if(props.datalistinfo[b]){
        var img_src = props.datalistinfo[b].img;
        var title = props.datalistinfo[b].title;
        var id = props.datalistinfo[b].id;
    }  
    function collection(){
        console.log(props.userinfo);
        if(props.userinfo.name=='张三'){
            props.dispatch({
                type:'datalist_collection',
                collectlist:{
                    id:id,
                    title:title
                }
            })
        }
        else{
            props.history.push('/login');
        }
    }

    return (
    <div className='body'>
        <div className='top' >
            <img
                className='goback'
                src={require('./carousel/左箭头.png')}
                onClick={() =>{props.history.push('/')}}/>
            <img
                className='gouwu'
                src={require('./carousel/购物车.png')} />
        </div>
        
        <div>
            <img src={img_src} style={{ width: '375px', height: '375px' }} alt='哎呀没有加载出来呢亲😙'/>
        </div>
        <div>
            <p>{title?title:'哎呀没有加载出来呢亲😙'}</p>
        </div>
        <div className='bottom'>
            <div className='icon-1'>
                <img src={require('./carousel/dianpu.png')}/>
                <div>店铺</div>
            </div>
            <div className='icon-1'>
                <img src={require('./carousel/kefu.png')} />
                <span>客服</span>
            </div>
            <div className='icon-1' onClick ={collection} >
                <img src={require('./carousel/xingxing.png')} />
                <span>收藏</span>
            </div>
            <div className='shop'>
                <div className='left'>
                    <span>加入购物车</span>
                </div>
                <div className='right'>
                    <span>领劵购买</span>
                </div>
            </div>
        </div>
    </div>
    );
}


export default connect(
    state=>({  
        datalistinfo:state.to_get,
        collection:state.to_collection,
        userinfo:state.to_login
    })
)(Details);
