import React, { Component } from 'react'
import {connect} from 'react-redux'
import store from './store'

function login(props){
    function getuserinfo(){
        fetch('https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login')
            .then(res=>res.json())
            .then(res=>{
                //console.log(res.data.name)
                props.dispatch({
                    type:'datalist_login',
                    userinfo:{
                        name:res.data.name
                    }
                })
                props.history.push('/');
            })
    }
    return (
        <div>
            <div>
                <p>{props.userinfo ? props.userinfo.name:'您还没有登陆呢'}</p>
            </div>
            <p>
                <input type="text"/>
            </p>
            <p>
                <input type="password"/>
            </p>
            <p> 
                <button  onClick={getuserinfo}>登录</button>
            </p>
        </div>
    )
}
export default connect(
    state=>({  
        userinfo:state.to_login
    })
)(login);