class GridExample extends Component {
    constructor(props) {
        super(props);
      }
    render() {
    return (
    <div  style={{background: '#e5e5e5'}}>
        <Grid data={data.datalist}
              columnNum={2}
              hasLine={false}
              renderItem={dataItem => (
                  <NavLink to={{pathname:`/home/details/${dataItem.id}`}}>
                      <div className='commodity'>
                          <img src={dataItem.img} style={{ width: '120px', height: '120px' }} alt="" />
                          <div style={{ color: 'black', fontSize: '20px', marginTop: '12px', height: '60px'  }}>
                              <span>{dataItem.title}</span>
                              <span style={{ color: 'rgb(241, 123, 12)', fontSize: '20px',marginTop: '12px' }}>{'$'+dataItem.price}</span>
                              {/*<span style={{ color: '#888',fontSize: '20px' ,marginTop: '12px' }}>1156人购买</span>*/}
                          </div>
                      </div>
                  </NavLink>
              )}
        />
    </div>
    );
}
}
