import React from 'react';
import { SearchBar} from 'antd-mobile';

const search = (props) => {
    return (<div>
        <SearchBar
            placeholder="寻找宝贝店铺"
            onCancel={() => {props.history.push('/')}}
            onClear={value => console.log(value, 'onClear')}
            onBlur={() => console.log('onBlur')}
            maxLength={20}
            showCancelButton={ true }
        />
    </div>);
}
export default search
