import React ,{Component}from 'react'
import img from '../assets/banner.jpg'
import Ca from "./Carousel";
import {NavLink} from "react-router-dom";
import './home.css'
import GridExample from "./Gongge";
// iconfont.cn官网搜选图标，加入购物车
// 点击页面右上角购物车，将图标添加进项目（没有项目就新建）
// 点击“下载到本地”
// 解压压缩包，全部复制到项目public文件夹下，
// 在index.html中通过link引入iconfont.css
// 使用时，<i className='iconfont icon-具体名字'></i>
const Home = () => {
    return (
        <div>
            <NavLink to='/home/search'>
                <div className='search'>
                    <div className='search-inside'>
                        <span>寻找宝贝店铺</span>
                    </div>
                </div>
            </NavLink>
            <Ca/>
            <img src={img} alt=""/>
            <GridExample/>
        </div>
    )
}

export default Home
