import { combineReducers } from 'redux'
import {
  datalist_get,
  datalist_collection,
  datalist_show,
  datalist_login,
  datalist_delete
} from './action'
let datalist={

}
function to_get(state =datalist, action) {
  switch (action.type) {
    case datalist_get:
        datalist=action.datalist.date;
        //shoucang=action.datalist.date;
        return datalist;
    default:
      return state
    }
}

let shoucang=[
]


function to_collect(state =shoucang, action) {
  switch (action.type) {
    case datalist_collection:
      //shoucang.id=action.collectlist.id;
      //shoucang.title=action.collectlist.title;
      console.log(shoucang);
      shoucang.push({
        'id' : action.collectlist.id,
        'title' : action.collectlist.title,
      })
      return shoucang
    case datalist_show:
      //shoucang.splice(action.collection_delete.id,1)
      console.log(action.collection_delete.id)
      return shoucang
    case datalist_delete:
        let arr =[...state];
        arr.splice(action.collection_delete.id,1)
        console.log(action.collection_delete.id)
        return arr
    default:
      return state
    }
}



let userinfo = {
  name:''
}


function to_login(state=userinfo,action){
  switch(action.type){
    case datalist_login:
      userinfo.name = action.userinfo
      console.log(action.userinfo)
      return action.userinfo;
    default:
      return state;
  }
}





export default combineReducers({
  to_get,to_collect,to_login
});