import React from 'react';
import { BrowserRouter as Router, Redirect, Route, NavLink, Switch } from 'react-router-dom'
import Mytab from './taobao/Mytab';
import Home from './taobao/Home';
import Cart from './taobao/Cart';
import search from "./taobao/Searchbar";
import Own from "./taobao/Own";
import Order from "./taobao/Order";
import Details from "./taobao/datial";
import login from "./taobao/login"
import { Provider } from "react-redux";
import store from "./taobao/store/index";
const App = ()=>{
	return <Provider store={store}>
	<Router basename ="/build">
		<Switch>
			<Route exact path='/' component={Home} />
			<Route path='/home/search' component={search} />
			<Route path='/home/details/:id' component={Details} />
			<Route path='/login' component={login} />
			<Route path='/own' component={Own} />
			<Route path='/cart' component={Cart} />
			<Route path='/order' component={Order} />
		</Switch>
		<Mytab/>
	</Router>
	</Provider>
}

export default App;


